.header {
  position: relative;
  z-index: 2;

  &.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    background-color: $colorWhite;
  }
}

.header-container {
  max-width: 100%;
}

.header-wrapper {
  padding: 18px 30px 18px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-logo {
  width: 99px;
  height: auto;
}

.header-logo img {
  width: 100%;
  height: auto;
}

.header-burger {
  cursor: pointer;
  border: none;
  outline: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  width: 35px;
  height: 35px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.header-burger svg {
  transition: opacity 0.5s ease-in-out;
}

.header-burger svg:nth-of-type(2) {
  height: 0;
  opacity: 0;
}

.header-burger.active svg:nth-of-type(1) {
  height: 0;
  opacity: 0;
}

.header-burger.active svg:nth-of-type(2) {
  height: unset;
  opacity: 1;
}

.header-btn {
  margin-top: 8px;
}

.header-left {
  padding-right: 9px;
  display: flex;
  align-items: center;
  column-gap: 50px;
}

.header-link {
  margin-top: 12px;
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 17px;
  font-style: normal;
  font-weight: 500;
  line-height: 25px;
  letter-spacing: 0.51px;
  text-transform: uppercase;
}

.header-right {
  display: flex;
  align-items: center;
  column-gap: 50px;
}

// Mobile Menu START
.mobile-menu {
  position: fixed;
  inset: 0;
  max-height: 100vh;
  background: $colorWhite;
  transform: translateY(-100%);
  transition: transform 0.3s ease;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
}

.mobile-menu.active {
  transform: translateY(0);
}

body:has(.mobile-menu.active) {
  overflow: hidden;
}

.mobile-menu ul li:not(:last-child) {
  margin-bottom: 12px;
}

.mobile-menu ul li {
  text-align: center;
}

.mobile-menu ul li a {
  margin-top: 12px;
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 17px;
  font-style: normal;
  font-weight: 500;
  line-height: 25px;
  letter-spacing: 0.51px;
  text-transform: uppercase;
}
// Mobile Menu END

@media only screen and (min-width: 768px) {
  .header-wrapper {
    padding: 18px 121px 18px 109px;
  }

  .header-logo {
    margin-top: 8px;
    width: 170px;
  }

  .header-burger {
    width: 52px;
    height: 52px;
    flex-shrink: 0;
  }
}

@media only screen and (min-width: 1280px) {
  .header-wrapper {
    padding: 23px 77px 23px 77px;
  }

  .header-logo {
    margin-top: 22px;
    width: 260px;
  }

  .header-btn {
    margin-top: 0;
    min-width: 221px;
  }

  .header-left {
    min-width: 30%;
  }

  .header-right {
    min-width: 30%;
    display: flex;
    justify-content: flex-end;
  }
}

@media only screen and (min-width: 1920px) {
  .header-container {
    max-width: 1404px;
    margin: 0 auto;
  }

  .header-wrapper {
    padding: 39px 0 0 0;
  }

  .header-logo {
    margin-top: 0;
    width: 310px;
  }

  .header-link {
    margin-top: 19px;
  }

  .header-btn {
    margin-top: 9px;
  }
}