::input-placeholder {
	color: $colorLightGrey;
	opacity: 1;
}

::placeholder {
	color: $colorLightGrey;
	opacity: 1;
}

:input-placeholder {
	color: $colorLightGrey;
	opacity: 1;
}

:placeholder {
	color: $colorLightGrey;
	opacity: 1;
}

input,
textarea {
	position: relative;
	background: $colorWhite;
	color: $colorBlack;
	width: 100%;
	font-size: 14px;
	font-family: "Helvetica Neue";
	border: 1px solid $colorGrey;
	transition: all .3 ease;
	padding: 0 20px;
	appearance: none;

	&:hover {
		border-color: $colorDarkGrey;
	}

	&:active,
	&:focus {
		&:not(.btn) {
			&:not([disabled]) {
				&::input-placeholder {
					color: transparent;
				}

				&::placeholder {
					color: transparent;
				}

				&:input-placeholder {
					color: transparent;
				}

				&:placeholder {
					color: transparent;
				}
			}
		}
	}

	&[disabled] {
		opacity: .5;
	}
}

input {
	height: 44px;
	text-overflow: ellipsis;
	overflow: hidden;
}

textarea {
	height: 110px;
	padding: 15px;
	resize: none;
}

.has {
	&-error {
		input,
		textarea {
			color: $colorRed !important;
			border-color: $colorLightRed !important;

			&::input-placeholder {
				color: $colorLightRed !important;
			}

			&::placeholder {
				color: $colorLightRed !important;
			}

			&:input-placeholder {
				color: $colorLightRed !important;
			}

			&:placeholder {
				color: $colorLightRed !important;
			}
		}
	}

	&-success {
		input,
		textarea {
			color: $colorGreen !important;
			border-color: $colorGreen !important;
		}
	}
}

.form {
	&-title {
		position: relative;
	}

	&-body {
		position: relative;
	}

	&-text {
		position: relative;
	}

	&-group {
		position: relative;

		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}

	&-button {
		position: relative;

		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}
}

.checkbox {
	position: relative;
	display: inline-block;

	[type="checkbox"],
	[type="radio"] {
		position: absolute;
		left: -9999px;
		width: inherit;
		height: inherit;

		& ~ label {
			position: relative;
			display: inline-block;
			min-height: 20px;
			color: #1a1a1a;
			line-height: 20px;
			padding-left: 20px;
			margin: -5px 0;
			cursor: pointer;

			&:before {
				content: "";
				position: absolute;
				background: $colorWhite;
				top: 50%;
				left: 0;
				width: 20px;
				height: 20px;
				border: 1px solid #c8c8c8;
				border-radius: 0;
				transform: translateY(-50%);
				transition: all .3s ease;
			}

			&:after {
				content: "";
				position: absolute;
				background: #ccc;
				top: 50%;
				left: 4px;
				width: 12px;
				height: 12px;
				transform: translateY(-50%);
				transition: all .3s ease;
				opacity: 0;
			}

			&:hover {
				&:before {
					border-color: #999;
				}
			}

			&:not(:empty) {
				margin: 0;
				padding-left: 30px;
			}
		}

		&:disabled {
			& ~ label {
				&:before{

				}

				&:after {
					opacity: 0;
				}
			}
		}

		&:checked {
			& ~ label {
				&:before{

				}

				&:after {
					opacity: 1;
				}
			}

			&:disabled {
				& ~ label {
					&:before{

					}

					&:after {
						opacity: .2;
					}
				}
			}
		}
	}

	[type="radio"] {
		& ~ label {
			&:before,
			&:after {
				border-radius: 50%;
			}
		}

		&:checked {
			& ~ label {
				&:before{

				}
			}
		}
	}

	&.has {
		&-error {
			[type="checkbox"],
			[type="radio"] {
				& ~ label {
					color: $colorRed !important;

					&:before {
						border-color: $colorLightRed !important;
					}
				}
			}
		}
	}
}

.help-block {
	position: absolute;
	top: 100%;
	width: 100%;
	color: $colorRed;
	font-size: 12px;
	line-height: 16px;
	padding: 0 30px;
}

// Radio START
.form-radio {}

.form-radio:not(:last-child) {
	margin-bottom: 30px;
}

.form-radio label {
  display: flex;
  align-items: center;
  column-gap: 14px;
  cursor: pointer;

  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.form-radio label:not(:last-child) {
	margin-bottom: 30px;
}

.form-radio label {}

.form-radio + img {
  max-width: 100vw;
  width: 100vw;
  margin-left: -30px;
  margin-right: -30px;
}

.form-radio label input {
	display: none;
}

.form-radio label input + span {
	flex-shrink: 0;
  width: 30px;
  height: 30px;
  border: 1px solid $blueDream;
  border-radius: 50%;
  position: relative;
}

.form-radio label input[type="radio"]:checked + span::after {
	content: "";
  width: 12px;
  height: 12px;
  background: $blueDream;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}

@media only screen and (min-width: 768px) {
  .form-radio label {
    column-gap: 20px;
    font-size: 14px;
  }

  .form-radio + img {
    max-width: 100%;
		width: 100%;
    margin-left: unset;
    margin-right: unset;
  }
}

@media only screen and (min-width: 1280px) {
  .form-radio label {
    font-size: 16px;
  }
}
// Radio END