.steps {
  padding-top: 137px;
}

.steps-container {}

.steps-head {}

.steps-wrapper {
  padding: 0 30px;
}

.steps-title {
  position: relative;
  color: $greyDream;
  text-align: center;
  font-family: "Helvetica Neue";
  font-size: 16px;
  font-style: italic;
  font-weight: 500;
  line-height: 125%;
  letter-spacing: 1px;
  text-transform: uppercase;
  display: block;
  width: max-content;
}

.steps-title svg {
  width: 57px;
  height: 60.032px;
  position: absolute;
  right: -16px;
  top: -10px;
  z-index: -1;
}

.steps-description {
  margin-top: 15px;
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 135%;
}

.steps-body {
  margin-top: 35px;
}

.steps-body-head {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 35px;
}

.steps-body-head-step {
  display: flex;
  flex-direction: column;
}

.steps-body-title {
  display: flex;
  flex-direction: column;
  row-gap: 4px;
  font-family: "Helvetica Neue";
  font-size: 16px;
  font-style: normal;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  line-height: 125%;
}

.steps-body-title span {
  color: $greyDream;
  font-weight: 500;
}

.steps-body-title span.light {
  color: $blueDream;
  font-weight: 700;
}


.steps-body-head-radios {
  margin-top: 25px;
}

.steps-body-foot-radios {
  margin-top: 27px;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 50px;
}

.steps-body-foot-radio {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.steps-body-foot {
  margin-top: 50px;
}

.steps-body-foot-title {
  color: $greyDream;
  text-align: center;
  font-family: "Helvetica Neue";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 50px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.steps-body-foot-step {
  margin-top: 25px;
}


.steps-body-foot-price {
  margin-top: 50px;
}

.steps-body-foot-price-head {
  position: relative;
  width: max-content;
  margin: 0 auto;
}

.steps-body-foot-price-title {
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 50px;
  text-transform: uppercase;
}

.steps-body-foot-price-icon {
  width: 70px;
  height: 66.08px;
  position: absolute;
  right: -16px;
  top: -8px;
  z-index: -1;
}

.steps-body-foot-price-icon svg {}

.steps-body-foot-price-btns {
  margin-top: 36px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 21px;
}

.steps-body-foot-price-btn {}

@media only screen and (min-width: 768px) {
  .steps {
    padding-top: 172px;
  }

  .steps-wrapper {
    padding: 0 109px;
  }

  .steps-title {
    font-size: 24px;
    margin:  0 auto;
  }

  .steps-title svg {
    width: 94px;
    height: 99px;
    right: -26px;
    top: -20px;
  }

  .steps-description {
    margin-top: 30px;
    text-align: center;
    font-size: 14px;
    line-height: 145%;
  }

  .steps-body {
    margin-top: 50px;
  }

  .steps-body-head {
    row-gap: 71px;
  }

  .steps-body-title {
    font-size: 20px;
  }

  .steps-body-foot-radios {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    row-gap: 70px;
  }

  .steps-body-foot-title {
    font-size: 24px;
  }

  .steps-body-foot-step {
    margin-top: 30px;
  }

  .steps-body-foot-price-btns {
    margin-top: 31px;
    flex-direction: row;
    column-gap: 27px;
    justify-content: center;
  }
}

@media only screen and (min-width: 1280px) {
  .steps {
    padding-top: 200px;
  }

  .steps-head {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    column-gap: 24px;
  }
  
  .steps-wrapper {
    padding: 0 81px;
  }

  .steps-title {
    flex-shrink: 0;
    margin: 0;
    font-size: 40px;
  }

  .steps-title svg {
    right: -18px;
    top: unset;
    bottom: -34px;
  }

  .steps-description {
    max-width: 550px;
    text-align: left;
    font-size: 16px;
  }

  .steps-body {
    margin-top: 100px;
  }

  .steps-body-head {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 48px;
  }

  .steps-body-title {
    min-height: 100px;
    line-height: 30px;
  }

  .steps-body-head-radios {
    margin-top: 35px;
  }

  .steps-body-foot-radios {
    margin-top: 50px;
    grid-template-columns: repeat(3, 1fr);
  }

  .steps-body-foot-title {
    font-size: 40px;
  }

  .steps-body-foot-step {
    margin-top: 50px;
  }

  .steps-body-foot-price {
    margin-top: 116px;
  }

  .steps-body-foot-price-title {
    font-size: 40px;
  }

  .steps-body-foot-price-icon {
    right: -24px;
    top: -16px;
  }

  .steps-body-foot-price-btns {
    margin-top: 45px;
  }
}

@media only screen and (min-width: 1440px) {
  .steps-wrapper {
    padding: 0 161px;
  }

  .steps-body {
    margin-top: 100px;
  }
}

@media only screen and (min-width: 1920px) {
  .steps-container {
    max-width: 1136px;
    margin: 0 auto;
  }

  .steps-wrapper {
    padding: 0;
  }

  .steps-body {
    margin-top: 100px;
  }

  .steps-body-foot-price-btns {
    flex-direction: row-reverse;
  }
}