.hero {}

.hero-container {}

.hero-wrapper {}

.hero-head {
  margin-top: 50px;
}

.hero-head-img {
  width: 100%;
  height: auto;
}

.hero-head-img img {
  width: 100%;
  height: auto;
}

.hero-body {
  margin-top: 40px;
  padding: 0 32px;
}

.hero-body-title {
  font-size: 16px;
  font-style: italic;
  font-weight: 500;
  line-height: 125%;
  letter-spacing: 1px;
}

.hero-body-text {
  margin-top: 10px;
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 135%;
}

.hero-body-btn {
  margin: 0 auto;
  margin-top: 20px;
  width: max-content;
}

@media only screen and (min-width: 768px) {
  .hero-head {
    margin-top: 70px;
    margin-left: 50px;
  }

  .hero-body {
    margin-top: 50px;
    padding: 0 109px;
  }

  .hero-body-title {
    text-align: center;
    font-size: 24px;
  }

  .hero-body-text {
    margin-top: 15px;
    text-align: center;
    font-size: 14px;
    line-height: 145%;
  }

  .hero-body-btn {
    margin-top: 35px;
  }
}

@media only screen and (min-width: 1280px) {
  .hero-head {
    margin-top: 74px;
    margin-left: 0;
  }

  .hero-body {
    padding: 0;
    padding-left: 92px;
    max-width: 670px;
    margin: 0 auto;
    margin-top: 5px;
  }

  .hero-body-title {
    text-align: left;
    font-size: 36px;
  }

  .hero-body-text {
    margin-top: 15px;
    text-align: left;
    font-size: 16px;
    line-height: 145%;
  }

  .hero-body-btn {
    margin: 0;
    margin-top: 35px;
    margin-left: 20px;
  }
}

@media only screen and (min-width: 1440px) {
  .hero-head {
    margin-left: 100px;
    margin-right: 100px;
  }

  .hero-body {
    padding-left: 116px;
    max-width: 676px;
  }
}

@media only screen and (min-width: 1920px) {
  .hero-head {
    max-width: 1402px;
    margin-top: 85px;
    margin-right: auto;
    margin-left: auto;
  }

  .hero-body {
    margin-top: 35px;
    padding-left: 114px;
  }


  .hero-body-btn {
    margin-left: 0;
  }
}