.btn {
	border-radius: 22.5px;
	min-width: 170px;
  min-height: 45px;
	color: $greyDream;
	font-family: "Helvetica Neue";
	font-size: 12px;
	font-style: normal;
	text-align: center;
	display: flex;
  justify-content: center;
  align-items: center;
	font-weight: 400;
	line-height: 20px;
	letter-spacing: 1.5px;
	text-transform: uppercase;
	cursor: pointer;
	text-decoration: none;
	transition: all 0.3s ease;
	outline: none;

	&-fill {
		background: $pinkDream;
		border: 2px solid $pinkDream;

			&:hover,
			&:active {
				background: $btnHoverBG;
				border: 2px solid $btnHoverBG;
			}
	}

	&-outline {
		background: transparent;
		border: 2px solid $pinkDream;

			&:hover,
			&:active {
				background: $pinkDream;
				border: 2px solid $pinkDream;
			}
	}

	&:hover,
	&:active {
		color: $greyDream;
		outline: none;
	}

	&:focus {
		outline: none;
	}

	&.disabled,
	&[disabled] {
		pointer-events: none;
		opacity: .5;
		cursor: auto;
	}
}

@media only screen and (min-width: 1280px) {
	.btn {
		min-width: 290px;
    min-height: 54px;

		border-radius: 27px;
    font-size: 16px;
	}
}