$colorWhite: #fff;
$colorBlack: #000;
$colorGrey: #666;
$colorLightGrey: #999;
$colorDarkGrey: #333;
$colorGreen: #4ab020;
$colorLightGreen: #99c71f;
$colorDarkGreen: #648300;
$colorRed: #b01e1e;
$colorLightRed: #ce7f7f;
$colorDarkRed: #881616;

$greyDream: #242424;
$pinkDream: #FCBEDD;
$blueDream: #AEBFE7;
$linkHover: #778071;
$btnHoverBG: #ddacc4;

html,
body {
	height: 100%;
}

body {
	color: $colorBlack;
	font-size: 14px;
	font-family: "Helvetica Neue";
	line-height: 1.25;
}

html {
	scroll-behavior: smooth;
}

* {
	outline: none;
	box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: $greyDream;
	font-family: "Helvetica Neue";
	text-transform: uppercase;
	font-size: 1em;
	line-height: 1;
	margin: 0;
}

img {
	vertical-align: top;
	max-width: 100%;
}

a {
	text-decoration: none;
	transition: none;

	&:hover {
		text-decoration: none;
		color: $linkHover;
	}
}

ul,
ol {
	list-style-type: none;
	list-style-position: inside;
	padding: 0;
	margin: 0;
}

p {
	margin: 0;
}

button {
	outline: none;

	&:focus {
		outline: none;
	}
}

[class^="icon-"],
[class*=" icon-"] {
	position: relative;
	vertical-align: middle;
}

.wrapper {
	position: relative;
	min-width: 320px;
	min-height: 100%;
	overflow: hidden;
}

// Hide Classes START
@media only screen and (min-width: 320px) and (max-width: 767px) {
  .h-320-767 {
		display: none !important;
	}
}
@media only screen and (min-width: 768px) and (max-width: 1279px) {
  .h-768-1279 {
		display: none !important;
	}
}
@media only screen and (min-width: 1280px) and (max-width: 1439px) {
  .h-1280-1439 {
		display: none !important;
	}
}
@media only screen and (min-width: 1440px) {
  .h-1440-more {
		display: none !important;
	}
}
// Hide Classes END