.footer {
  padding: 25px 0 30px 0;
  background-color: $pinkDream;
}

.footer-container {
  padding: 0 30px;
}

.footer-wrapper {}

.footer-instagram {
  display: flex;
  column-gap: 30px;
  align-items: flex-start;
}

.footer-instagram-title {
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0.36px;
}

.footer-instagram-links {
  display: flex;
  flex-direction: column;
}

.footer-instagram-links a {
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0.36px;
  text-decoration: underline;
}

.footer-menu {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  row-gap: 5px;
}

.footer-menu a {
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0.36px;

  &:hover {
    color: $linkHover;
  }
}

.footer-copyright {
  margin-top: 30px;
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0.36px;
}

.footer-wss {
  display: block;
  margin-top: 5px;
  position: relative;
}

.footer-wss svg {}

.footer-wss-popup {
  display: block;
  position: absolute;
  background: #f7f7f7;
  border: solid 1px #141414;
  bottom: calc(100% + 4px);
  width: 160px;
  color: #001a11;
  font-size: 12px;
  line-height: 1.25;
  padding: 5px 10px;
  transition: all .5s ease;
  z-index: -1;
  visibility: hidden;
  opacity: 0;
}

.footer-wss:hover .footer-wss-popup {
  visibility: visible;
  opacity: 1;
  z-index: 1;
}

@media only screen and (min-width: 768px) {
  .footer {
    padding: 30px 0 28px 0;
  }

  .footer-container {
    padding: 0 109px;
  }

  .footer-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 60px;
    row-gap: 30px;
  }

  .footer-menu {
    margin-top: 0;
  }

  .footer-copyright {
    margin-top: 0;
  }
}

@media only screen and (min-width: 1280px) {
  .footer {
    padding: 22px 0;
  }

  .footer-container {
    padding: 0 80px;
  }

  .footer-wrapper {
    display: flex;
    justify-content: center;
    grid-template-columns: unset;
    column-gap: unset;
    row-gap: unset;
  }

  .footer-instagram-title {
    font-size: 17px;
    letter-spacing: 0.51px;
  }

  .footer-instagram-links a {
    font-size: 17px;
    letter-spacing: 0.51px;
  }

  .footer-menu {
    row-gap: unset;
  }

  .footer-menu a {
    font-size: 17px;
    letter-spacing: 0.51px;
  }

  .footer-copyright {
    margin-right: 76px;
    font-size: 16px;
    letter-spacing: 0.48px;
  }

  .footer-wss {
    margin-top: 0;
    margin-left: 50px;
  }

  .footer-wss-popup {
    transform: translateX(-100%);
  }
}

@media only screen and (min-width: 1440px) {
  .footer-container {
    padding: 0 164px;
  }
}

@media only screen and (min-width: 1920px) {
  .footer-container {
    padding: 0;
    max-width: 1136px;
    margin: 0 auto;
  }
}
