.program {
  padding-top: 135px;
  padding-bottom: 135px;
}

.program-container {
  padding: 0 30px 0 15px;
}

.program-wrapper {}

.program-img-wrapper {
  position: relative;
}

.program-img {
  width: 129px;
  height: auto;
}

.program-img img {
  width: 100%;
  height: auto;
}

.program-list {}

.program-title {
  width: 184px;
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 16px;
  font-style: italic;
  font-weight: 500;
  line-height: 125%;
  letter-spacing: 1px;
  text-transform: uppercase;
  position: absolute;
  bottom: 0;
  right: 0;
}

.program-items {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.program-item {
  position: relative;
}

.program-item-icon {
  width: 38px;
  height: 36px;
  position: absolute;
  top: -8px;
  left: 0;
}

.program-item-icon svg {}

.program-item-text {
  padding-left: 62px;
}

.program-item-title {
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 125%;
  letter-spacing: 0.6px;
  text-transform: uppercase;
}

.program-item-description {
  margin-top: 8px;
  color: $greyDream;
  font-family: "Helvetica Neue";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 135%;
}

.program-button {
  margin-top: 25px;
  justify-self: center;
}

@media only screen and (min-width: 768px) {
  .program {
    padding-top: 153px;
    padding-bottom: 166px;
  }

  .program-container {
    padding: 0 50px 0 108px;
  }


  .program-img {
    width: 342px;
  }

  .program-title {
    width: auto;
    font-size: 24px;
    padding-left: 200px;
  }

  .program-items {
    margin-top: 56px;
  }

  .program-item-text {
    padding-left: 78px;
  }

  .program-item-title {
    font-size: 20px;
  }

  .program-item-description {
    font-size: 16px;
    line-height: 145%;
  }

  .program-button {
    margin-top: 30px;
  }
}

@media only screen and (min-width: 1280px) {
  .program {
    padding-top: 198px;
    padding-bottom: 200px;
  }

  .program-container {
    padding: 0 210px 0 24px;
  }

  .program-wrapper {
    display: flex;
    column-gap: 42px;
  }

  .program-img-wrapper {
    flex-shrink: 0;
  }

  .program-img {
    width: 357px;
  }

  .program-list {}

  .program-title {
    position: static;
    width: 100%;
    font-size: 40px;
  }

  .program-items {
    margin-top: 100px;
  }

  .program-item-text {
    padding-left: 74px;
  }

  .program-button {
    margin-left: 35px;
    margin-top: 55px;
    justify-self: flex-start;
    font-weight: 500;
  }
}

@media only screen and (min-width: 1440px) {
  .program-container {
    padding: 0 290px 0 104px;
  }

  .program-img {
    width: 394px;
  }
}

@media only screen and (min-width: 1920px) {
  .program {
    padding-top: 200px;
    padding-bottom: 208px;
  }

  .program-container {
    padding: 0;
    max-width: 1136px;
    margin: 0 auto;
  }

  .program-wrapper {
    max-width: 1044px;
  }

  .program-img {
    width: 394px;
  }
}
